export default class Logger {
  constructor({
    name = '',
    stream = process.stdout,
  } = {}) {

  }

  greet() {
    return 'hello';
  }
}

Logger.prototype.console = console;

Object.keys(console).forEach(methodName => {
  const method = console[methodName];
  Logger.prototype[methodName] = function proxyPrint() {
    this.console[methodName](...arguments);
  };
});



